<template>
  <el-container>
    <el-main v-loading="loading">
      <el-table :data="userInfoList" align="center">
        <el-table-column prop="id" label="用户ID" />
        <el-table-column prop="userName" label="用户姓名" />
        <el-table-column prop="paper" label="证件号码" />
        <el-table-column prop="mobile" label="手机号" />
        <el-table-column prop="address" label="用户地区" />
        <el-table-column prop="cardNum" label="保单号/服务号" />
        <el-table-column prop="companyId" label="所属企业" />
        <el-table-column prop="status" label="状态" />
        <el-table-column prop="youServers" label="当前服务" />
        <el-table-column label="操作" width='100'>
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
            <el-button type="text" size="small" @click="disPath(scope.row)">派单</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </el-container>
</template>
<script>
import { userManageInfo } from "../../../../data/userManage";
export default {
  name: "listTable",
  props:['data', 'loading'],
  data() {
    return {
      userInfoList: Array(8).fill(userManageInfo),
      // userInfoList: [],
      id:''
    };
  },
  methods: {
    handleClick(params) {
      this.id = params.id;
      this.$router.push(`userDetail?type=edit&id=${this.id}`);
    },
    disPath(params) {
      this.id = params.id;
      this.$router.push(`userDetail?type=disPath&id=${this.id}`);
    }
  },
  watch:{
    data(newValue){
      this.hospitalList = newValue;
    }
  }
};
</script>
